$orSeperator-text-bg-color: $White;
$orSeperator-text-color: $DustyGray;
$orSeperator-before-bg-color: $Alto;

.o-or {
  position: relative;

  display: flex;
  justify-content: center;

  margin-bottom: 20px;

  .o-orText {
    position: relative;
    z-index: z("middle-floor");

    padding: 0 $xs;

    background-color: $orSeperator-text-bg-color;
    color: $orSeperator-text-color;

    font-size: $fs-14;
  }

  &::before {
    content: " ";

    position: absolute;
    top: 50%;
    left: 0;

    width: 100%;
    height: 1px;

    background-color: $orSeperator-before-bg-color;
  }
}
