$button-border-color: $Niagara;
$button-text-color-primary: $White;
$button-text-color-secondary: $Niagara;
$button-bg-color-primary: $Niagara;
$button-bg-color-secondary: $White;
$button-icon-border-color: $Alto;
$button-icon-text-color: $Emperor;
$loading-bg-color: $White;
$btnSizes: ("large", "medium", "small");
$btnTypes: ("primary", "secondary");
$btnIcons: ("facebook", "google");
$fontSize: (
    "large": $fs-16,
    "medium": $fs-14,
    "small": $fs-12
  );
$height: (
    "large": 50px,
    "medium": 40px,
    "small": 30px
  );
$border: (
    "primary": none,
    "secondary": 1px solid $button-border-color
  );
$color: (
    "primary": $button-text-color-primary,
    "secondary": $button-text-color-secondary
  );
$backgroundColor: (
    "primary": $button-bg-color-primary,
    "secondary": $button-bg-color-secondary
	);
$btnIconUrls: (
	"facebook": url("/img/icons/facebook.svg"),
	"google": url("/img/icons/google.svg")
);

@mixin btnSize ($size, $excludeList: ()) {
  @if index($excludeList, "height") == null {
    height: map-get($height, $size);
  }

  @if index($excludeList, "font-size") == null {
    font-size: map-get($fontSize, $size);
  }
}

@mixin btnType ($type, $excludeList: ()) {
  @if index($excludeList, "border") == null {
    border: map-get($border, $type);
  }

  @if index($excludeList, "background-color") == null {
    background-color: map-get($backgroundColor, $type);
  }

  @if index($excludeList, "color") == null {
    color: map-get($color, $type);
  }
}

@mixin btnIcon ($iconName) {
  position: relative;

  padding-left: 60px;
  border: 3px solid $button-icon-border-color;

  color: $button-icon-text-color;

  font-size: $fs-14;
  text-align: left;

  @include btnSize("large", ("font-size"));
  @include btnType("secondary", ("border", "color"));

  &::before {
    content: " ";

    position: absolute;
    top: 50%;
    left: 20px;

    width: 20px;
    height: 20px;

    background-image: map-get($btnIconUrls, $iconName);
    background-repeat: no-repeat;
    background-position: center left;

    transform: translateY(-50%);
  }
}

.m-btn {
  width: 100%;

  margin-bottom: 20px;

  font-weight: $bolder;
  text-align: center;

  border-radius: 2px;

  @each $size in $btnSizes {
    &.-#{$size} {
      @include btnSize($size);
    }
  }

  @each $type in $btnTypes {
    &.-#{$type} {
      @include btnType($type);
    }
  }

  @each $icon in $btnIcons {
    &.-#{$icon} {
      @include btnIcon($icon);
    }
  }

  &.-loading {
    display: flex;
    justify-content: center;
    align-items: center;

    .a-bounce1 {
      animation-delay: -.32s;
    }

    .a-bounce2 {
      animation-delay: -.16s;
    }

    @keyframes btnLoading-bouncedelay {
      0%,
      80%,
      100% {
        transform: scale(0);
      } 40% {
        transform: scale(1.0);
      }
    }

    & > div {
      display: flex;
      width: 12px;
      height: 12px;

      margin-right: 3px;

      background-color: $loading-bg-color;

      @include border-radius(50%);
      animation: btnLoading-bouncedelay 1.4s infinite ease-in-out both;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}
