$formTabs-border-color: $Alto;
$formTabs-active-border-color: $Niagara;
$formTabs-active-text-color: $CodGray;
$formTabs-tab-text-color: $Emperor;

.o-formTabs {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 54px;

  padding: 0 $lg;
  border-bottom: 1px solid $formTabs-border-color;

  .m-tab {
    width: 100%;
    height: 54px;

    color: $formTabs-tab-text-color;

    font-family: $Avenir;
    font-size: $fs-14;
    font-weight: $bolder;
    line-height: 54px;
    text-align: center;

    &.-active {
      border-bottom: 2px solid $formTabs-active-border-color;

      color: $formTabs-active-text-color;
    }
  }

  .m-tabRadio {
    display: none;
  }
}
