$error-bg-color: $Cinderella;
$error-text-color: $CodGray;

.o-error {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;

  padding: $lg;

  background-color: $error-bg-color;

  opacity: 0;
  visibility: hidden;

  transition: opacity .2s ease-in, visibility .2s ease-in;

  &Text {
    color: $error-text-color;

    font-size: $fs-14;
    line-height: 18px;
  }

  &.-show {
    opacity: 1;
    visibility: visible;
  }
}
