$input-text-color: $CodGray;
$input-focused-border-color: $CodGray;
$input-error-text-color: $Cinnabar;
$input-error-border-color: $Cinnabar;
$input-label-text-color: $Emperor;
$input-error-message-text-color: $Cinnabar;
$input-border-color: $Alto;

.o-inputWrapper {
  position: relative;

  width: 100%;

  margin-top: $lg;

  .m-input {
    width: 100%;
    height: 50px;

    padding: $md $md 0;
    border: 1px solid $input-border-color;

    color: $input-text-color;

    font-family: $Avenir;
    font-size: $fs-14;
    line-height: $fs-18;

    border-radius: 2px;

    &:focus {
      border-color: $input-focused-border-color;
    }

    &:focus ~ .m-label,
    &:not(:placeholder-shown) ~ .m-label {
      top: $sm;

      font-size: $fs-10;
      font-weight: $bolder;
      line-height: $fs-14;

      transform: none;
    }

    &.-err {
      border: 1px solid $input-error-text-color;
    }
    &.-err ~ .m-label {
      color: $input-error-border-color;
    }
  }
  .m-label {
    position: absolute;
    top: 25px;
    left: $md;

    color: $input-label-text-color;

    font-family: $Avenir;
    font-size: $fs-14;
    line-height: $fs-18;

    transition: .2s ease all;
    pointer-events: none;
    transform: translateY(-50%);
  }

  .-errMsg {
    margin-top: $xs;

    color: $input-error-message-text-color;

    font-family: $Avenir;
    font-size: $fs-12;
    line-height: $fs-16;
  }

  .m-inputOverBtn {
    position: absolute;
    top: 19px;
    right: 18px;

    height: 12px;

    border: none;

    background-color: none;
  }
}
